import React from "react"
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import {Link} from 'react-router-dom';

export default class Effects extends React.Component
{
    render() {
        return (
            <div className='help-page'>
                 <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/help">
                        Help Center
                    </Link>
                    <Link to="/help/editing">
                        Editing
                    </Link>
                    <Link to="/help/effects">
                        Effects
                    </Link>
                </Breadcrumbs>
                <h2>Post-Processing Effects</h2>
                <p>
                    Post processing effects are special effects applied to the final rendered image of each frame.
                </p>
                <h4>Bloom</h4>
                <p>
                    The Bloom effect makes bright part of an object glow, such as neon lights when viewed through a camera.<br/>
                </p>
                <img alt='configurator' src={require('assets/images/help/bloom.jpg')} />                 
                <h5>Options</h5>
                <ul>
                    <li>Strength - the intensity of glow</li>
                    <li>Threshold - determines which areas glow</li>
                    <li>Radius - the radius of the glow area</li>
                </ul>
                <h4>Depth of field</h4>
                <img alt='configurator'src={require('assets/images/help/dof.jpg')} />                 
                <p>
                    This simulates how camera lenses focus at a certain distance while everything else is out of focus and blurred.
                </p>
                <h5>Options</h5>
                <ul>
                    <li>Focus - distance to the point of focus</li>
                    <li>Apertue - the smaller the value is, the shallower the depth of field is</li>
                    <li>blur - amount of blur used</li>
                </ul>
            </div>
        );
    }
}